* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: katari;
  font-weight: 700;
}

a {
  color: inherit;
}

img, video {
  display: block;
}

header {
  height: 100vh;
  display: flex;
  position: relative;
}

footer {
  border-top: 1px solid #fff;
}

.header-section-1 {
  background-image: url("../img/background-wall.0f76c4a0.webp");
  background-size: cover;
  flex-direction: column;
  justify-content: space-between;
  min-width: 1300px;
  padding: 20px 10vh 40px 16px;
  display: flex;
}

.header-section-1 .container-1 {
  align-items: flex-start;
  display: flex;
}

.header-section-1 .container-1 img:first-child {
  height: 40vh;
  max-height: 450px;
}

.header-section-1 .container-2 div + img {
  height: 50vh;
  max-height: 500px;
}

.header-section-1 .container-2 {
  justify-content: center;
  align-items: flex-end;
  display: flex;
}

.header-section-2-background {
  height: 100vh;
}

.header-section-3-background {
  background-image: url("../img/painting-4.16ff2a7f.webp");
  background-position: center;
  background-size: cover;
  width: 100vw;
  height: 100vh;
}

.pillar img {
  width: 280px;
}

.pillar {
  z-index: 10;
  height: 100%;
  position: absolute;
  overflow: hidden;
  transform: translateX(-10%);
}

.bubble-header, .bubble {
  text-align: center;
  justify-content: center;
  font-size: 24px;
  display: flex;
}

.bubble-header p, .bubble p {
  align-items: center;
  padding: 0 35px;
  display: flex;
  position: absolute;
}

.bubble-header-1, .bubble-header-1 p {
  width: 408px;
  height: 182px;
}

.bubble-header-2, .bubble-header-2 p {
  width: 399px;
  height: 175px;
}

.bubble-header-3, .bubble-header-3 p {
  width: 399px;
  height: 175px;
  position: absolute;
}

.bubble-header-3 {
  margin: 45px 120px;
}

.bubble-header-4, .bubble-header-4 p {
  width: 399px;
  height: 209px;
  position: absolute;
}

.bubble-header-4 {
  margin: 30px 150px;
}

.bubble-fracture {
  z-index: 15;
  height: 0;
  position: relative;
  top: -200px;
}

.bubble-fracture p {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 30vw;
  margin-top: 2.5vw;
  padding: 0 40px;
  font-size: 48px;
  display: flex;
  position: absolute;
}

.bubble-fracture img {
  width: 100vw;
}

.bubble p {
  width: 400px;
  margin-top: 85px;
}

.header-section-1 .bubble-header {
  margin: 64px 28px;
}

.bubble-climax {
  position: relative;
  top: 500px;
  left: 860px;
}

.bubble-climax p {
  text-align: center;
  width: 292px;
  margin-top: 10px;
  font-size: 24px;
  position: absolute;
}

.bubble-climax img {
  width: 292px;
}

body {
  overflow-x: hidden;
}

.horizontal-scroll-container {
  width: 100vw;
  height: 100vh;
}

.horizontal-scroll {
  width: 100vw;
  height: 100vh;
  display: flex;
}

.castle-section {
  width: 100vw;
  height: 800px;
}

.castle-info-cumbe {
  margin-top: 70px;
}

.castle-background {
  background-image: url("../img/background-castle.4eb7fab4.webp");
  background-position: bottom;
  background-size: cover;
  width: 100%;
  height: 2000px;
  position: absolute;
}

.castle-middleground {
  z-index: 5;
  width: 100%;
  margin-top: 1000px;
  position: absolute;
}

.castle-bubble-king {
  transform: translateY(-100px);
}

.castle-bubble-cumbe {
  margin-top: 485px;
}

.castle-img-cumbe {
  margin-top: 200px;
}

.castle-middleground img {
  width: 100%;
}

.castle-foreground {
  background-image: url("../img/foreground-castle.c61b8256.webp");
  background-position: center;
  background-size: cover;
  height: 85vh;
}

.castle-foreground-container {
  z-index: 10;
  flex-direction: column;
  justify-content: flex-end;
  width: 100%;
  height: 800px;
  display: flex;
  position: absolute;
}

.sister-section {
  color: #fff;
  background: #0c0c0c;
  font-size: 24px;
}

.sister-info {
  height: 419px;
  padding: 165px 0 0;
}

.sister-info div div div:last-child {
  position: relative;
  top: 140px;
}

.sister-sword {
  width: 100%;
  transition: all 1s;
  position: relative;
  top: 70px;
  right: -400px;
  transform: scale(.55);
}

.sister-background {
  background-image: url("../img/king-sword.1a67b98c.webp");
  background-position: bottom;
  background-size: cover;
  flex-direction: column;
  justify-content: flex-end;
  height: 80vh;
  display: flex;
}

.end-climax {
  background-image: url("../img/background-isa-sword.cd1338ee.webp");
  background-position: bottom;
  background-size: cover;
  width: 50%;
  height: 100vh;
}

.end-section {
  width: 200vw;
  transition: all .5s;
  display: flex;
}

.end-shop {
  color: #fff;
  background: #000;
  flex-direction: column;
  justify-content: space-between;
  width: 100vw;
  height: 100vh;
  font-size: 32px;
  display: flex;
}

.end-info-container {
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 90px;
  display: flex;
}

.end-info-container img {
  width: 129px;
}

.end-info-container .cta {
  margin-top: 20px;
}

.end-shop-logo-furieuse {
  width: 400px;
}

.end-book {
  height: 561px;
}

.end-book img {
  height: 100%;
}

.end-shop-info {
  padding: 95px 0 0 75px;
}

.end-shop-info .end-shop-trailer p {
  padding-top: 40px;
}

.end-container-logo svg {
  width: 15px;
  height: 20px;
}

.end-container-logo svg:hover path {
  fill: #ffffffa1;
}

.end-container-logo {
  justify-content: flex-end;
  gap: 40px;
  display: flex;
}

.end-footer-container {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.end-footer {
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  font-size: 16px;
  display: flex;
}

.end-footer p, .end-footer a {
  font-family: akshar, sans-serif;
  font-weight: 400;
}

.end-footer a:hover {
  color: #ffffffa1;
  text-decoration: none;
}

.end-section.active {
  transform: translateX(-50%);
}

.barrel img:first-child {
  z-index: 10;
  margin-top: 70px;
  position: absolute;
}

.barrel img:nth-child(2) {
  z-index: 5;
  margin-top: 55px;
  position: absolute;
}

.barrel-head {
  transition: all 2s;
}

.barrel-active {
  transition: all .5s;
  transform: translateY(-55px);
}

.cta {
  border: 1px solid #fff;
  border-radius: 5px;
  padding: 10px 18px;
  transition: all .3s;
}

.cta-shop, .cta-start {
  color: #fff;
  background: #0000005c;
  position: fixed;
  top: 30px;
  right: 30px;
}

.cta:hover {
  color: #000;
  background: #fff;
}

.hide {
  display: none;
}

#start {
  visibility: hidden;
  position: absolute;
}

.stop-section {
  z-index: 50;
  width: 0;
  height: 0;
  position: relative;
  left: 1096px;
}

.stop-section div {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.stop-section img, .stop-section p {
  position: absolute;
}

.stop-section p {
  z-index: 200;
  width: 800px;
  margin-top: 230px;
  font-size: 217px;
  left: 370px;
}

.stop-move {
  transition: all .2s;
  position: relative;
  left: 1450px;
}

.stop-character {
  width: 386px;
  top: 200px;
  left: 1050px;
}

.stop-bubble {
  width: 2000px;
  left: -300px;
}

